// import './main.styl'

class Slider {
	constructor (options) {
		const defaultOption = {
			// 过度动画类型 slide, fade
			animationType: 'slide',
			// 过度动画时长
			animationDuration: 1000,
			// 自定义类名
			className: 'my-slider',
			// 轮播频率
			duration: 3000,
			// 标签颜色
			handlerActiveColor: '#F96048',
			handlerColor: '#E6E6E6',
			// 是否使用滑动事件
			touchEvent: false,
			height: '200px',
			width: '100vw',
		}
		this.options = {
			...defaultOption,
			...options
		}

		// 初始化
		this.init()
	}

	// 初始化
	init () {
		const {
			ele,
			width,
			height,
			className,
			animationType,
			touchEvent,
		} = this.options
		this.$ele = document.getElementById(ele)
		// 盒子不存在的情况
		if (!this.$ele) {
			console.error('slider box does not exist')
			return
		}

		this.$ele.className = `${className} slider__type--${animationType} slider`
		this.$ele.style.height = height
		this.$ele.style.width = width

		this.initContent()
		this.initHandler()
		this.initAnimation()
	}

	// 控制监听currentIndex来
	get currentIndex () {
		return this.index || 0
	}

	set currentIndex (nextIndex) {
		const currentIndex = this.currentIndex
		const { images, handlerColor, handlerActiveColor } = this.options
		const $images = document.getElementsByClassName('slider__images__item')
		const $handler = document.getElementsByClassName('slider__handlers__item')

		let direction = ''

		if (nextIndex > images.length - 1) {
			nextIndex = 0
		} else if (nextIndex < 0) {
			nextIndex = images.length - 1
		}
		// 判断方向
		if ((nextIndex < currentIndex && this.userEvent) || this.userEvent) {
			direction = 'reverse'
			this.userEvent = false
		}
		$images[currentIndex].className = `slider__images__item slider__images__item--inactive ${direction}`
		$handler[currentIndex].className = `slider__handlers__item slider__handlers__item--inactive`
		$handler[currentIndex].style.background = handlerColor

		$images[nextIndex].className = `slider__images__item slider__images__item--active ${direction}`
		$handler[nextIndex].className = `slider__handlers__item slider__handlers__item--active`
		$handler[nextIndex].style.background = handlerActiveColor

		this.index = nextIndex
	}

	// 绑定滑动事件
	initSlideEvent (imageDom) {
		let position = {
			startX: 0,
			startY: 0,
			endX: 0,
			endY: 0,
		}
		imageDom.addEventListener('touchstart', (evt) => {
			const evtPosition = evt.targetTouches[0] || {}
			position.startX = evtPosition.clientX
			position.startY = evtPosition.clientY
		})
		imageDom.addEventListener('touchmove', (evt) => {
			const evtPosition = evt.targetTouches[0] || {}
			position.endX = evtPosition.clientX
			position.endY = evtPosition.clientY
		})
		imageDom.addEventListener('touchend', () => {
			const { endX, endY, startX, startY } = position
			const clientW = document.body.clientWidth
			const offsetX = endX - startX
			const offsetY = endY - startY

			if (Math.abs(offsetX) > clientW * 0.3 && offsetY < 80 && endX) {
				clearInterval(this.timer)
				this.userEvent = startX < endX
				this.currentIndex += startX < endX ? -1 : 1
				this.initAnimation()

				position = {}
			}
		})
	}

	// 初始化handler点击切换
	initHandlerEvent (handlerDom) {
		handlerDom.addEventListener('touchstart', evt => {
			const index = Array.from(handlerDom.children).findIndex(o => o === evt.target)
			if (index !== -1) {
				clearInterval(this.timer)
				this.currentIndex = index
				this.initAnimation()
			}
		})
	}

	// 初始化图片
	initContent () {
		const {
			className,
			images,
			touchEvent,
			animationDuration,
		} = this.options
		let result = ''
		images.forEach((item, index) => {
			result += `<li
				class="slider__images__item"
				style="background-image: url(${item}); z-index: ${images.length + 2 - index}; animation-duration: ${animationDuration}ms"
			/>`
		})

		const ulDom = document.createElement('UL')
		ulDom.className = 'slider__images'
		ulDom.innerHTML = result

		if (touchEvent) this.initSlideEvent(ulDom)
		this.$ele.appendChild(ulDom)
	}

	// 初始化循环动画
	initAnimation () {
		this.timer = setInterval(() => {
			this.currentIndex += 1
		}, this.options.duration)
	}

	// 初始化滚动句柄
	initHandler () {
		const {
			images,
			handlerColor,
			handlerActiveColor,
			handlerSize,
			animationDuration,
		} = this.options
		const handleList = []
		let result = ''

		for (let i = 0; i < images.length; i++) {
			result += `<li
				class="slider__handlers__item slider__handlers__item--${i ? 'inactive' : 'active'}"
				style="background: ${i ? handlerColor : handlerActiveColor}; animation-duration: ${animationDuration}ms"
			/>`
		}

		const ulDom = document.createElement('UL')
		ulDom.className = 'slider__handlers'
		ulDom.innerHTML = result

		this.initHandlerEvent(ulDom)
		this.$ele.appendChild(ulDom)
	}
}

module.exports = Slider



